<template>
  <div>
    <mheader></mheader>
    <div class="breadcrumb">
      <a-breadcrumb>
        <a-breadcrumb-item><router-link to="/">Home</router-link></a-breadcrumb-item>
        <a-breadcrumb-item>Friend Links</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <h1 class="title">MLDIE's Friends</h1>
    <div class="links">
      <span><a href="https://www.google.com/" target="_blank">TestDATA Google</a></span>
      <span><a href="https://www.baidu.com/" target="_blank">TestDATA Baidu</a></span>
      <span v-for="item in friendLink" :key="item.id"><a :href="item.url" target="_blank">{{item.name}}</a></span>
    </div>
    <mfooter></mfooter>
  </div>
</template>

<script>
import axios from '@/api';
import Mheader from '@/components/Mheader';
import Mfooter from '@/components/Mfooter';

export default {
  components: {
      Mheader,
      Mfooter,
  },
  data() {
    return {
      value: '# index',
      friendLink: [],
    }
  },
  computed: {
    language() {
      return localStorage.lang == 'en-US' ? 'en' : localStorage.lang;
    }
  },
  mounted() {
    axios.getFriendLink().then(res => {
      this.friendLink = res;
    });
  }
}
</script>

<style scoped>
.breadcrumb {
  margin-top: 3rem;
}
.title {
  text-align: center;
  font-weight: bold;
  margin-top: 3rem;
  /* color: #777; */
  color: rgba(1, 1, 1, .5)
}
.links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.links span {
  display: inline-block;
  flex: 0 1 auto;
  /* font-size: 1.5rem; */
  padding: 1rem;
}
</style>
